import React from "react";
import GithubCorner from "react-github-corner";

export default () => (
  <div>
    <header>
      <GithubCorner
        href="https://github.com/mburakerman/css3-checkbox-switch-generator/"
        target="_blank"
        bannerColor="#555"
      />

      <h1 className="title">CSS3 Checkbox Switch Generator</h1>
      <h4 className="description">
         Turn an input checkbox into a toggle without any additional elements
        in a second! 
      </h4>
      <h4 className="description"> Cross Browser </h4>
      <h4 className="description">
        Inspired from 
        <a href="https://codepen.io/rakeizer/pen/QQRBoZ" target="_blank">
          https://codepen.io/rakeizer/pen/QQRBoZ
        </a>
      </h4>
    </header>
  </div>
);



